import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Vasya from '@/components/Vasya'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Hello',
            component: Hello
        },
        {
            path: '/vasya',
            name: 'Tut byl vasya',

            component: Vasya
        },
        {
            path: '/meetup/:meetupId/:meetingId',
            name: 'meetingExpand',
            component: Hello
        }
    ]
})



